<template>
  <div class="testpage">
    <div class="page-panel">
      <pem-scroll-view ref="scroll" class="scroll-view">
        <div class="deal">
          <div class="btn" @click="clearSdPhone">清除圣诞和新年锦鲤手机号</div>
          <div class="btn" @click="clearLxPhone">清除拉新手机号</div>
          <div class="btn" @click="callAndroid">测试调用Android方法</div>
          <div class="btn" @click="callAndroid2">测试调用Android方法2</div>
          <div class="btn" @click="callIos">测试调用IOS方法</div>
          <div class="btn" @click="callIos2">测试调用IOS方法</div>

          <p class="txt txt-success" v-html="approotStr"></p>
          
          <p class="txt txt-success">浏览器是否支持 localstorage: {{ supportLocalStorage }}</p>
          
          
          <a class="btn btn-danger" href="iOSTencentTest://" style="">iOSTencentTest:// (ios a标签)</a>
          <a class="btn btn-danger" href="IOSXMGC://" style="">IOSXMGC:// (ios a标签)</a>
          <a class="btn btn-danger" href="pemarket://www.pemarket.com.cn/attr?type=3&id=26">pemarket://www.pemarket.com.cn/attr?type=3&id=26 (Android a标签)</a>
          <a class="btn btn-danger" href="https://itunes.apple.com/cn/app/id1057083949?mt=8">项目工场 Apple Store 地址 (https://itunes.apple.com/cn/app/id1057083949?mt=8)</a>
          <a class="btn btn-danger" href="https://pem.mynatapp.cc/agreement?type=1&id=renyun">https://pem.mynatapp.cc/agreement?type=1&id=renyun</a>
          <a class="btn btn-danger" href="https://go.pemarket.com.cn/agreement?type=1&id=renyun">https://go.pemarket.com.cn/agreement?type=1&id=renyun</a>

          <div class="btn" @click="testIos">window.location.href = 'IOSXMGC://' (ios)</div>
          <div class="btn" @click="testIos2">window.location.href = 'iOSTencentTest://' (ios)</div>
          <div class="btn" @click="testAndroid">window.location.href = 'pemarket://www.pemarket.com.cn/attr?type=3&id=26' (android)</div>
          <div class="btn" @click="goAgree">window.location.href = 'https://go.pemarket.com.cn/agreement?type=1&id=renyun'</div>
          <div class="btn" @click="testAtag">测试通过 a 标签跳转到其他页面</div>
          
          <p class="txt txt-success">userAgent: {{ uaString }}</p>
          <!-- 相关判断 -->
          <ul class="ua-list">
            <li class="ua-item" v-for="(value, name) in deviceObj" :key="name">
              {{ name }}: {{ value }}
            </li>
          </ul>

          <p class="txt txt-success">storageStr: {{storageStr}}</p>
          <p class="txt txt-success">testStrorage: {{testStrorageStr}}</p>
          <p class="txt txt-success">testCookie: {{testCookieStr}}</p>
        </div>
      </pem-scroll-view>
    </div>
    <pem-toast ref="toastEle" :text="toastTxt"></pem-toast>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import PemToast from 'components/pem-toast/pem-toast'
import { device } from 'common/js/util'
import storage from 'common/js/storage.js'

export default {
  components: {
    PemScrollView,
    PemToast
  },
  data () {
    return {
      approotStr: '',
      toastTxt: '',
      supportLocalStorage: !!window.localStorage,
      msg: '',
      device: device,
      isIOS: device.isIOS(),
      isAndroid: device.isAndroid(),
      storageStr: '',
      testStrorageStr: '',
      testCookieStr: '',
      uaString: window.navigator.userAgent,
      deviceObj: {
        isClient: device.isClient(),
        isIOS: device.isIOS(),
        isAndroid: device.isAndroid(),
        isUCBrowser: device.isUCBrowser(),
        isQQBrowser: device.isQQBrowser(),
        isWeiBo: device.isWeiBo(),
        isWeixin: device.isWeixin(),
        isAlipay: device.isAlipay(),
        isPC: device.isPC(),
        qqBrowserVersion: device.qqBrowserVersion(),
        ucBrowserVersion: device.ucBrowserVersion()
      }
    }
  },
  created () {
    let that = this
    console.log('id:', this.$route.query.id)
    this.storageStr = storage.get('__pemweb__minisite__auth__' || null)
    storage.set('testStrorageStr', 'renyuntestlocalstorage')
    document.cookie = 'testCookieStr=renyuntestCookieStr'
    this.testCookieStr = document.cookie
    setTimeout(() => {
      that.testStrorageStr = storage.get('testStrorageStr' || null)
      that.$refs.scroll.refresh()
    }, 2000)
    if (!device.isWeixin()) {
      if (device.isAndroid()) {
        // this.testAndroid()
      }
      if (device.isIOS()) {
        // this.testIos()
      }
    }
  },
  mounted () {
    window.testH5 = this.goAgree
  },
  methods: {
    callAndroid () {
      this.approotStr = window.approot + ''
      // if (window.approot) {
      //   this.approotStr += `\n dataurl:${window.approot.getImageUrlAddr()}`
      //   this.approotStr += `\n severurl:${window.approot.getBaseServerAddr()}`
      // }
      let commentData = {}
      commentData.parameters = {}
      commentData.parameters.testPara = 'newFunction'
      commentData.callBack = 'testH5'
      let commentStr = JSON.stringify(commentData)
      window.approot.getCouponList(commentStr)
      // let commentData = {}
      // commentData.URLString = 'projectcomment/commentlist'
      // commentData.parameters = {}
      // commentData.parameters.projectzj = '013B77F1A10F41C9A00654FA4FD7D7B2'
      // commentData.parameters.pageSize = '3'
      // commentData.parameters.currentPage = '1'
      // commentData.parameters.position = 'detail'
      // commentData.callBack = 'testH5'
      // let commentStr = JSON.stringify(commentData)
      // window.approot.pdaUniversalEvent(commentStr)
    },
    callAndroid2 () {
      this.approotStr = window.approot + ''
      // if (window.approot) {
      //   this.approotStr += `\n dataurl:${window.approot.getImageUrlAddr()}`
      //   this.approotStr += `\n severurl:${window.approot.getBaseServerAddr()}`
      // }
      let commentData = {}
      commentData.URLString = 'vip/getCouponByNewYear'
      commentData.parameters = {}
      commentData.parameters.position = 'test'
      commentData.callBack = 'testH5'
      let commentStr = JSON.stringify(commentData)
      window.approot.pdaUniversalEvent(commentStr)
    },
    callIos () {
      try {
        // window.webkit.messageHandlers.determineTheLogin.postMessage({testPara: '200'})
        // let commentData = {}
        // commentData.URLString = 'projectcomment/commentlist'
        // commentData.parameters = {}
        // commentData.parameters.projectzj = '013B77F1A10F41C9A00654FA4FD7D7B2'
        // commentData.parameters.pageSize = '3'
        // commentData.parameters.currentPage = '1'
        // commentData.parameters.position = 'detail'
        // commentData.callBack = 'testH5'
        // window.webkit.messageHandlers.executeNativeRequest.postMessage(commentData)
        let commentData = {}
        commentData.parameters = {}
        commentData.parameters.testPara = 'newFunction'
        commentData.callBack = 'testH5'
        window.webkit.messageHandlers.getCouponList.postMessage(commentData)
      } catch (error) {
        alert(error)
      }
    },
    callIos2 () {
      try {
        // window.webkit.messageHandlers.determineTheLogin.postMessage({testPara: '200'})
        // let commentData = {}
        // commentData.URLString = 'projectcomment/commentlist'
        // commentData.parameters = {}
        // commentData.parameters.projectzj = '013B77F1A10F41C9A00654FA4FD7D7B2'
        // commentData.parameters.pageSize = '3'
        // commentData.parameters.currentPage = '1'
        // commentData.parameters.position = 'detail'
        // commentData.callBack = 'testH5'
        // window.webkit.messageHandlers.executeNativeRequest.postMessage(commentData)
        let commentData = {}
        commentData.URLString = 'vip/getMyCouponList'
        commentData.parameters = {}
        commentData.parameters.type = 100
        commentData.parameters.pageSize = 10
        commentData.parameters.currentPage = 1
        commentData.callBack = 'testH5'
        window.webkit.messageHandlers.executeNativeRequest.postMessage(commentData)
      } catch (error) {
        alert(error)
      }
    },
    testIos () {
      console.log('test IOSXMGC://')
      window.location.href = 'IOSXMGC://'
    },
    testIos2 () {
      console.log('test iOSTencentTest://')
      window.location.href = 'iOSTencentTest://'
    },
    testAndroid () {
      console.log('test android')
      window.location.href = 'pemarket://www.pemarket.com.cn/attr?type=3&id=26'
    },
    goAgree (resObj) {
      if (typeof resObj === 'string') {
        this.approotStr += `\n${resObj}\nisIOS:${this.isIOS};isAndroid:${this.isAndroid}`
        // let arr = JSON.parse(resObj)
        // for (let i = 0; i < arr.length; i++) {
        //   let item = arr[i]
        //   this.approotStr += `${i}- amount:${typeof item.amount}${item.amount} start_use_time:${typeof item.start_use_time}${item.start_use_time} and ${JSON.stringify(resObj)}`
        // }
      } else {
        this.approotStr += `\nisIOS:${this.isIOS};isAndroid:${this.isAndroid}\nreturnValue:${typeof resObj} and ${JSON.stringify(resObj)}`
      }
      // window.location.href = `https://www.baidu.com/s?ie=UTF-8&wd=${typeof keyword} and ${keyword.loginSucceed}`
      // window.location.href = 'https://ng.ant.design/docs/introduce/en'
      // window.location.href = 'https://pem.mynatapp.cc/agreement?type=1&id=renyun'
    },
    testAtag () {
      let atag = document.createElement('a')
      atag.link = 'https://pem.mynatapp.cc/agreement?type=1&id=renyun'
      document.body.appendChild(atag)
      atag.click()
    },
    clearLxPhone () {
      storage.remove('lxphone')
      this.showToast('清除成功')
    },
    clearSdPhone () {
      storage.set('sdphone', '')
      storage.set('newphone', '')
      this.showToast('清除成功')
    },
    showToast (txt) {
      if (!txt) return
      this.toastTxt = txt
      this.$refs.toastEle.show()
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .deal
    padding: 15px
    font-size: 15px
    .ua-item
      text-align: center
      color: #faad14;
      line-height: 30px;
    .red
      color: #f5222d;
    .yellow
      color: #faad14;
    .txt
      box-sizing: border-box;
      margin: 0;
      padding: 8px 15px
      color: rgba(0,0,0,.65);
      font-size: 14px;
      font-variant: tabular-nums;
      line-height: 1.5;
      list-style: none;
      font-feature-settings: 'tnum';
      position: relative;
      word-wrap: break-word;
      border-radius: 4px;
      margin-bottom: 15px
      &.txt-success
        background-color: #f6ffed;
        border: 1px solid #b7eb8f;
      &.txt-info
        background-color: #e6f7ff;
        border: 1px solid #91d5ff;
      &.txt-warn
        background-color: #fffbe6;
        border: 1px solid #ffe58f;
      &.txt-error
        background-color: #fff1f0;
        border: 1px solid #ffa39e;
    .btn
      position: relative;
      display: inline-block 
      color: #fff;
      background-color: rgb(45, 183, 245);
      border-color: rgb(45, 183, 245);
      text-shadow: 0 -1px 0 rgba(0,0,0,.12);
      box-shadow: 0 2px 0 rgba(0,0,0,.045);
      margin: 0 10px 15px 0
      font-weight: 400;
      line-height: 1.5 
      text-align: center;
      background-image: none;
      border: 1px solid #d9d9d9;
      box-shadow: 0 2px 0 rgba(0,0,0,.015);
      cursor: pointer;
      transition: all .3s cubic-bezier(.645,.045,.355,1);
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
      touch-action: manipulation;
      padding: 10px 15px;
      font-size: 14px;
      border-radius: 4px;
      &.btn-danger
        color: #fff;
        background-color: rgb(255, 85, 0);
        border-color: rgb(255, 85, 0);
        text-shadow: 0 -1px 0 rgba(0,0,0,.12);
        box-shadow: 0 2px 0 rgba(0,0,0,.045);
</style>



